import { Box, Button } from '@material-ui/core'
import { DicomImage, Series } from 'common/types'
import { FILES_URL } from 'common/utils'
import { useImageSelectorStyles, useSelectableCardStyles } from 'PatientList/styles'
import React, { useCallback } from 'react'
import { ReactComponent as CheckedIcon } from 'assets/icon/checked.svg'
import { ReactComponent as UncheckedIcon } from 'assets/icon/unchecked.svg'

interface Props {
  activeSeries: Series
  selectedImageMap: Map<string, string>
  updateSelectedImageMap: (map: Map<string, string>) => void
  onView: () => void
}

const SelectableCard: React.FC<{
  image: DicomImage
  selected: boolean
  onToggle: (id: string) => void
}> = (props) => {
  const { image, selected, onToggle } = props
  const classes = useSelectableCardStyles()
  return (
    <div className={classes.root} onClick={() => onToggle(image.sop_inst_uid)}>
      <img
        src={`${FILES_URL}/${image.thumbnail}`}
        alt={'img-' + image.sop_inst_uid}
        draggable={false}
      />
      <span className={classes.icon}>{selected ? <CheckedIcon /> : <UncheckedIcon />}</span>
    </div>
  )
}

export default function ImageSelector(props: Props) {
  const { activeSeries, selectedImageMap, updateSelectedImageMap, onView } = props
  const classes = useImageSelectorStyles()

  const onToggleCard = useCallback(
    (imageId: string, studyId: string) => {
      const newMap = new Map(selectedImageMap)

      newMap.has(imageId) ? newMap.delete(imageId) : newMap.set(imageId, studyId)
      updateSelectedImageMap(newMap.size > 4 ? selectedImageMap : newMap)
    },
    [selectedImageMap, updateSelectedImageMap]
  )

  return (
    <div className={classes.root}>
      <Box display="flex" justifyContent="flex-end">
        <Button variant="contained" className={classes.viewBtn} onClick={onView}>
          {selectedImageMap.size > 0 ? `View detail(${selectedImageMap.size}/4)` : 'View all'}
        </Button>
      </Box>

      <Box
        className={classes.images}
        display="flex"
        alignItems="flex-start"
        flexWrap="wrap"
        overflow="auto"
      >
        {activeSeries.images.map((img) => (
          <SelectableCard
            key={'img-' + img.sop_inst_uid}
            image={img}
            selected={selectedImageMap.has(img.sop_inst_uid)}
            onToggle={(imageId) => onToggleCard(imageId, activeSeries.study_inst_uid)}
          />
        ))}
      </Box>
    </div>
  )
}
