import { useQuery } from '@apollo/client'
import { Box } from '@material-ui/core'
import { Schedule, Patient, ShiftId, GetScheduleParams } from 'common/types'
import dayjs from 'dayjs'
import { useCallback, useEffect, useState } from 'react'
import StudySelector from './components/StudySelector'
import PatientSelector from './components/PatientSelector'
import { GET_SCHEDULES, SCHEDULES_SUBSCRIPTION } from './gql'
import PatientFilter from './components/PatientFilter'

// FIXME: fake date for demo
const from = dayjs('2021-04-07T00:00:00Z')
const DEFAULT_SHIFT = 'AM'

export default function PatientList() {
  const { loading, error, data, subscribeToMore, refetch } = useQuery<
    { schedules: Schedule[] },
    GetScheduleParams
  >(GET_SCHEDULES, {
    // TODO: Query by clinic id
    variables: {
      from: from.toISOString(),
      to: from.endOf('day').toISOString(),
      shift_id: DEFAULT_SHIFT,
    },
  })
  const [shiftId, setShiftId] = useState<ShiftId>(DEFAULT_SHIFT)
  const [activePatientId, setActivePatientId] = useState<string | null>(null)

  useEffect(() => {
    if (!data?.schedules) {
      return
    }
    if (data.schedules.length < 100) return

    subscribeToMore<{ scheduleUpdated: { id: string; patients: Patient[] } }, { ids: string[] }>({
      document: SCHEDULES_SUBSCRIPTION,
      variables: {
        ids: data.schedules.map((s) => s.his_schedule_id),
      },
      updateQuery: (prev, { subscriptionData }) => {
        if (!subscriptionData.data) return prev
        // Find the updated schedule and replace its patient list
        const updatedScheduleId = subscriptionData.data.scheduleUpdated.id
        const newScheduleData = JSON.parse(JSON.stringify({ ...prev }))
        newScheduleData.schedules.forEach((s: Schedule) => {
          if (s.id === updatedScheduleId) {
            s.patients = subscriptionData.data.scheduleUpdated.patients
          }
        })
        return newScheduleData
      },
    })
  }, [subscribeToMore, data])

  const onShiftIdChange = useCallback(
    (id: ShiftId) => {
      setShiftId(id)
      refetch({
        from: from.toISOString(),
        to: from.endOf('day').toISOString(),
        shift_id: id,
      })
    },
    [refetch]
  )

  if (error) {
    return <>error</>
  }

  if (loading) {
    return <>Loading</>
  }

  return (
    <Box display="flex">
      <div>
        <PatientFilter
          shiftId={shiftId}
          schedules={data?.schedules ?? []}
          updateShiftId={onShiftIdChange}
          onSelectPatient={setActivePatientId}
        />
        <PatientSelector
          schedules={data?.schedules ?? []}
          activePatientId={activePatientId}
          onSelectPatient={setActivePatientId}
        />
      </div>
      {activePatientId && <StudySelector activePatientId={activePatientId} />}
    </Box>
  )
}
