import { gql } from '@apollo/client'

export const GET_SCHEDULES = gql`
  query GetSchedules($from: String, $to: String, $shift_id: String) {
    schedules(from: $from, to: $to, shift_id: $shift_id) {
      id
      his_schedule_id
      patients {
        name
        view_no
        his_patient_id
      }
    }
  }
`
export const SCHEDULES_SUBSCRIPTION = gql`
  subscription onScheduleUpdated($ids: [ID!]!) {
    scheduleUpdated(ids: $ids) {
      id
      patients {
        name
        view_no
        his_patient_id
      }
    }
  }
`

export const GET_PATIENT_STUDIES = gql`
  query GetPatientStudies($id: String!) {
    patientStudies(id: $id) {
      study_inst_uid
      created_at
      series {
        study_inst_uid
        series_inst_uid
        body_part_exam
        series_num
        created_at
        images {
          sop_inst_uid
          thumbnail
        }
      }
    }
  }
`
