import { usePatientSelectorStyles } from 'PatientList/styles'

import { Schedule } from 'common/types'

interface Props {
  schedules: Schedule[]
  activePatientId: string | null
  onSelectPatient: (id: string) => void
}

export default function PatientSelector(props: Props) {
  const { schedules, activePatientId, onSelectPatient } = props
  const classes = usePatientSelectorStyles()

  return (
    <section className={classes.selector}>
      {schedules.length > 0 &&
        schedules[0].patients.map((p) => {
          const isActive = activePatientId === p.his_patient_id
          return (
            <div
              key={p.his_patient_id}
              className={`${classes.patient} ${isActive ? 'active' : ''}`}
              onClick={() => onSelectPatient(p.his_patient_id)}
            >
              <span className="view-no">{p.view_no.toString().padStart(3, '0')}</span>
              <span>{p.name}</span>
            </div>
          )
        })}
    </section>
  )
}
