import { useQuery } from '@apollo/client'
import { Box } from '@material-ui/core'
import { BodyPartExam, Series, Study } from 'common/types'
import { parseStudiesByYear, ROUTES } from 'common/utils'
import dayjs from 'dayjs'
import { GET_PATIENT_STUDIES } from 'PatientList/gql'
import { useStudySelectorStyles } from 'PatientList/styles'
import { useCallback, useEffect, useState } from 'react'
import { generatePath } from 'react-router'
import BodyPartSelector, { BodyPartExamMap } from './BodyPartSelector'
import ImageSelector from './ImageSelector'

interface Props {
  activePatientId: string
}

export default function StudySelector(props: Props) {
  const { activePatientId } = props
  const classes = useStudySelectorStyles()

  const { loading, error, refetch } = useQuery<{ patientStudies: Study[] }, { id: string }>(
    GET_PATIENT_STUDIES,
    {
      variables: { id: activePatientId },
      skip: true, // Skip the default query and let useEffect to refetch
    }
  )
  const [studiesByYear, setStudiesByYear] = useState<Map<string, Study[]>>(new Map())
  const [activeSeries, setActiveSeries] = useState<Series | null>(null)
  const [selectedImageMap, setSelectedImageMap] = useState<Map<string, string>>(new Map()) // Map<image_id, study_id>
  const [filteredBodyPartExamMap, setFilteredBodyPartExamMap] = useState<BodyPartExamMap>(new Map())

  useEffect(() => {
    setActiveSeries(null)
    setSelectedImageMap(new Map())
    refetch({ id: activePatientId }).then((result) => {
      setStudiesByYear(parseStudiesByYear(result.data.patientStudies))
    })
  }, [activePatientId, refetch])

  const onFilterBodyPartExam = useCallback((bodyPartExamMap: BodyPartExamMap) => {
    setFilteredBodyPartExamMap(bodyPartExamMap)
    setActiveSeries(null)
    setSelectedImageMap(new Map())
  }, [])

  const onView = useCallback(() => {
    let imageIds = ''
    let studyIds = new Set<string>()

    // Find all the study id and remove duplicated by leveraging Set
    for (const studyGroup of studiesByYear.values()) {
      studyGroup.forEach(({ study_inst_uid }) => studyIds.add(study_inst_uid))
    }
    const studyIdsString = Array.from(studyIds).join(',')

    // Open detail pages with specified DICOM files if any
    if (selectedImageMap.size === 0) {
      imageIds = 'all'
    } else {
      imageIds = [...selectedImageMap.keys()].sort().join(',')
    }

    const url =
      generatePath(ROUTES.detail.url) +
      `?studyIds=${studyIdsString}&imageIds=${imageIds}&patientId=${activePatientId}`
    window.open(url)
  }, [activePatientId, selectedImageMap, studiesByYear])

  if (loading) {
    return <>Loading</>
  }

  if (error) {
    return <>Error</>
  }

  return (
    <Box display="flex" className={classes.root}>
      <div className={classes.studies}>
        <BodyPartSelector onFilter={onFilterBodyPartExam} />
        {[...studiesByYear.entries()].map(([year, studyGroup]) => (
          <Box key={year} mb="20px">
            <span className={classes.year}>{year}</span>
            {studyGroup.map((study) => (
              <div key={'study' + study.study_inst_uid}>
                {study.series.map((series) => {
                  // To ignore case sensitive and white space in the DICOM body_part_exam
                  const parsedBodyPartExam = series.body_part_exam
                    .toUpperCase()
                    .trim() as BodyPartExam

                  if (
                    filteredBodyPartExamMap.size > 0 &&
                    !filteredBodyPartExamMap.has(parsedBodyPartExam)
                  ) {
                    // User want to filter body parts, and this series is not fulfilled
                    return null
                  }

                  const isActive = activeSeries?.series_inst_uid === series.series_inst_uid
                  return (
                    <div
                      key={'series' + series.series_inst_uid}
                      className={`${classes.series} ${isActive ? 'active' : ''}`}
                      onClick={() => {
                        setActiveSeries(series)
                      }}
                    >
                      {dayjs(series.created_at).format('MM/DD')}
                    </div>
                  )
                })}
              </div>
            ))}
          </Box>
        ))}
      </div>
      {activeSeries && (
        <ImageSelector
          activeSeries={activeSeries}
          selectedImageMap={selectedImageMap}
          updateSelectedImageMap={setSelectedImageMap}
          onView={onView}
        />
      )}
    </Box>
  )
}
