import { Select, MenuItem, TextField, InputProps } from '@material-ui/core'
import Autocomplete from '@material-ui/lab/Autocomplete'
import { Schedule, ShiftId } from 'common/types'
import { usePatientFilterStyles } from 'PatientList/styles'
import React, { useCallback, useEffect, useState } from 'react'
import { ReactComponent as SearchIcon } from 'assets/icon/search.svg'

interface Props {
  shiftId: ShiftId
  schedules: Schedule[]
  updateShiftId: (id: ShiftId) => void
  onSelectPatient: (id: string | null) => void
}

export default function PatientFilter(props: Props) {
  const { shiftId, schedules, updateShiftId, onSelectPatient } = props
  const classes = usePatientFilterStyles()
  const [options, setOptions] = useState<{ his_patient_id: string; name: string }[]>([])

  useEffect(() => {
    if (schedules.length === 0) {
      return
    }
    setOptions(schedules[0].patients.map(({ his_patient_id, name }) => ({ his_patient_id, name })))
  }, [schedules])

  const onShiftIdChange = useCallback(
    (event: React.ChangeEvent<{ value: unknown }>) => {
      const newShiftId = event.target.value as ShiftId
      updateShiftId(newShiftId)
    },
    [updateShiftId]
  )

  return (
    <section className={classes.root}>
      <Autocomplete
        className={classes.search}
        freeSolo={true}
        clearOnBlur={true}
        options={options}
        classes={{ paper: classes.paper, option: classes.option }}
        onChange={(_, value) => {
          if (typeof value === 'object') {
            onSelectPatient(value?.his_patient_id ?? null)
          }
        }}
        getOptionLabel={(option) => {
          return typeof option === 'string'
            ? option
            : `${option.his_patient_id.padStart(8, '0')} ${option.name}`
        }}
        renderInput={(params) => {
          // We can not assign {...params} and InputProps at the same time
          // because it will be overwritten
          params.InputProps.startAdornment = <SearchIcon />
          ;(params.InputProps as InputProps).disableUnderline = true
          return <TextField {...params} placeholder="Search" />
        }}
      />
      <Select
        className={classes.select}
        classes={{ icon: classes.icon }}
        value="clinic_1"
        disableUnderline
        MenuProps={{ classes: { paper: classes.menu } }}
      >
        <MenuItem value="clinic_1">Clinic 1</MenuItem>
      </Select>
      <Select
        className={classes.select}
        classes={{ icon: classes.icon }}
        value={shiftId}
        onChange={onShiftIdChange}
        disableUnderline
        MenuProps={{ classes: { paper: classes.menu } }}
      >
        <MenuItem value="AM">Morning</MenuItem>
        <MenuItem value="PM">Afternoon</MenuItem>
        <MenuItem value="EVENING">Evening</MenuItem>
      </Select>
    </section>
  )
}
